import { createWebHistory, createRouter } from "vue-router";
import SearchEngine1 from './components/SearchEngine1.vue';
import Froad from './components/Froad.vue';
import Asearch from './components/Asearch.vue';
import Study from './components/forsearch1/Study.vue';
import Toilet from './components/forsearch1/Toilet.vue';
import Water from './components/forsearch1/Water.vue';
import Conven from './components/forsearch1/Conven.vue';
import Peace from './components/forsearch1/Peace.vue';
import Paper from './components/forsearch1/Paper.vue';
import Engineer1 from './components/forsearch1/Engineer1.vue';
import Engineer4f4 from './components/forsearch1/Engineer4f4.vue';
import Waysoul from './components/forsearch1/Waysoul.vue';
import Engineer4 from './components/forsearch1/Engineer4';
import ShuttleTime from './components/forAsearch1/ShuttleTime.vue';

const routes = [
  {
    path: `/main`,
    component: SearchEngine1,
  },
 {
        path: `/study`,
        component: Study,
        children:[
          {
            path: `공학관1`,
            component: Engineer1,
          },
          {
            path: `공학관4`,
            component: Engineer4,
          },
          {
            path: `외솔관`,
            component: Waysoul,
          },
        ]
      },
      {
        path: `/toilet`,
        component: Toilet,
        children:[
          {
            path: `공학관4`,
            component: Engineer4,
            children:[
              {
                path: `4`,
                component: Engineer4f4,
              }
            ]
            
          }
        ]
      },
      {
        path: `/water`,
        component: Water,
        children:[
          {
            path: `공학관1`,
            component: Engineer4,
            children:[
              {
                path: `4`,
                component: Engineer4f4,
              }
            ]
          },
          {
            path: `공학관4`,
            component: Engineer4,
          },
        ]
      },
      {
        path: `/conven`,
        component: Conven,
        children:[
          {
            path: `공학관1`,
            component: Engineer1,
          },
          {
            path: `공학관4`,
            component: Engineer4,
          },
        ]
      },
      {
        path: `/peace`,
        component: Peace,
        children:[
          {
            path: `공학관1`,
            component: Engineer1,
          },
          {
            path: `공학관4`,
            component: Engineer4,
          },
          {
            path: `외솔관`,
            component: Waysoul,
          },
         
        ]
      },
      {
        path: `/paper`,
        component: Paper,
        children:[
          {
            path: `공학관1`,
            component: Engineer1,
          },
          {
            path: `공학관4`,
            component: Engineer4,
          },
        ]
      },
    

  {
    path: `/froad`,
    component: Froad,
  },
  {
    path: `/asearch`,
    name: 'Asearch',
    component: Asearch,
  },
      {
        path: '/shuttletime',
        name: 'ShuttleTime',
        component: ShuttleTime,
      }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 