<template>
  <div id="search-app" class="search-container">
    <input
      type="text"
      id="search"
      v-model="searchQuery"
      @input="onSearch"
      placeholder="Search"
      class="search-input"
    />
    <ul v-if="suggestions.length" class="suggestions-list">
      <li
        v-for="suggestion in suggestions"
        :key="suggestion"
        @click="goToSuggestion(suggestion)"
        class="suggestion-item"
      >
        {{ suggestion }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchQuery: '',
      allSuggestions: [
        '셔틀 버스',
        '셔틀 버스 노선',
        '셔틀 버스 시간표',
        // 추가 가능한 다른 추천 검색어들
      ],
      suggestions: []
    };
  },
  methods: {
    onSearch() {
      if (this.searchQuery.trim() === '') {
        this.suggestions = [];
      } else {
        this.suggestions = this.allSuggestions.filter((suggestion) =>
          suggestion.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
      }
    },
    goToSuggestion(suggestion) {
      if (suggestion === '셔틀 버스 시간표') {
        this.$router.push({ name: 'ShuttleTime' });
      }
      if (suggestion === '셔틀 버스') {
        this.$router.push({ name: 'ShuttleTime' });
      }
      if (suggestion === '셔틀 버스 노선') {
        this.$router.push({ name: 'ShuttleTime' });
      }
      // 다른 추천 검색어에 대해서도 유사한 조건을 추가할 수 있습니다.
    }
  }
};
</script>

<style>
.search-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.search-input {
  width: 90%; /* 검색창의 너비 */
  padding: 10px 15px;
  font-size: 1rem;
  border: 1px solid #ccc; /* 경계선 색상 */
  border-radius: 20px; /* 검색창의 모서리를 둥글게 */
  margin-bottom: 20px; /* 검색창 아래의 공간 */
}

.search-input::placeholder {
  color: #ccc;
}

.suggestions-list {
  list-style-type: none;
  padding: 0;
  width: 100%;
}

.suggestion-item {
  padding: 5px 10px;
  cursor: pointer;
}

/* 활성화된 검색창 스타일 */
.search-input:focus {
  outline: none;
  border-color: blue; /* 포커스 됐을 때의 테두리 색상 */
  box-shadow: 0 0 5px blue; /* 포커스 됐을 때의 그림자 효과 */
}
</style>