<template>

  <div class="search-engine">
    <div class="tag-container">
      <div v-for="tag in tags" :key="tag" class="tag" :class="{ selected: selectedTag === tag }" @click="toggleTag(tag)">
        {{ tag }}
      </div>
    </div>

<div class="dropdown-container">
    <!-- 첫 번째 드롭다운: 건물 선택 -->
    <div v-if = "dropdownBuildings.length > 0">
      <select v-model="selectedBuilding" @change="updateFloorDropdown">
        <option disabled value="">건물을 선택하세요</option>
        <option v-for="building in dropdownBuildings" :key="building">{{ building }}</option>
      </select> 
    </div>

    <!-- 두 번째 드롭다운: 층수 선택 -->
    <div v-if = "dropdownFloors.length > 0">
      <select v-model="selectedFloor" @change="updateRoomDropdown">
        <option disabled value="">층을 선택하세요</option>
        <option v-for="floor in dropdownFloors" :key="floor">{{ floor }}</option>
      </select>
    </div>

    <!-- 세 번째 드롭다운: 방 선택 -->
    <div v-if = "dropdownRooms.length > 0">
      <select v-model="selectedRoom">
        <option disabled value="">방을 선택하세요</option>
        <option v-for="room in dropdownRooms" :key="room">{{ room }}</option>
      </select>
    </div>
     </div>

     <div>
       <br><br><br>


  <button type="button" class="btn btn-primary" @click="goToPage('study')">공부할래요</button><br><br>
  <button type="button" class="btn btn-primary" @click="goToPage('toilet')">화장실 갈래요</button><br><br>
  <button type="button" class="btn btn-primary" @click="goToPage('water')">목말라요</button><br><br>
  <button type="button" class="btn btn-primary" @click="goToPage('conven')">편의점/카페 찾아요</button><br><br>
  <button type="button" class="btn btn-primary" @click="goToPage('peace')">휴게실 갈래요</button><br><br>
  <button type="button" class="btn btn-primary" @click="goToPage('paper')">복사할래요</button><br><br>
     </div>
  </div>
  <div>
    <router-view></router-view>
  </div>

<Conven :selectedBuilding = "selectedBuilding" :selectedFloor = "selectedFloor" :selectedRoom = "selectedRoom"></Conven>
<Paper :selectedBuilding = "selectedBuilding" :selectedFloor = "selectedFloor" :selectedRoom = "selectedRoom"></Paper>
<Study :selectedBuilding = "selectedBuilding" :selectedFloor = "selectedFloor" :selectedRoom = "selectedRoom"></Study>
<Peace :selectedBuilding = "selectedBuilding" :selectedFloor = "selectedFloor" :selectedRoom = "selectedRoom"></Peace>
<Toilet :selectedBuilding = "selectedBuilding" :selectedFloor = "selectedFloor" :selectedRoom = "selectedRoom"></Toilet>
<Water :selectedBuilding = "selectedBuilding" :selectedFloor = "selectedFloor" :selectedRoom = "selectedRoom"></Water>

</template>

<script>
import buildingData from './buildingdata.js';

export default {
  data() {
    return {
      tags: ['ㄱ', 'ㄴ', 'ㄷ', 'ㄹ', 'ㅁ', 'ㅂ', 'ㅅ', 'ㅇ', 'ㅈ', 'ㅊ', 'ㅍ', 'ㅎ'],
      selectedTag: null,
      dropdownContents: [], // 드롭다운에 표시될 content
      selectedBuilding: '',
      selectedFloor: '',
      selectedRoom: '',
      dropdownBuildings: [],
      dropdownFloors: [],
      dropdownRooms: [],
      items : buildingData,
    };
  },
   methods: {
    toggleTag(tag) {
      this.selectedTag = this.selectedTag === tag ? null : tag;
      this.updateDropdown();
    },
    updateDropdown() {
      this.resetDropdowns();
      if (this.selectedTag) {
        const foundItem = this.items.find(item => item.name === this.selectedTag);
        if (foundItem) {
          this.dropdownBuildings = foundItem.content.map(c => c.building);
        }
      }
    },
    updateFloorDropdown() {
      this.dropdownFloors = [];
      this.selectedFloor = '';
      this.dropdownRooms = [];
      const foundBuilding = this.items
        .find(item => item.name === this.selectedTag)
        ?.content.find(c => c.building === this.selectedBuilding);
      if (foundBuilding) {
        this.dropdownFloors = Object.keys(foundBuilding.floors);
      }
    },
    updateRoomDropdown() {
      this.dropdownRooms = [];
      const foundBuilding = this.items
        .find(item => item.name === this.selectedTag)
        ?.content.find(c => c.building === this.selectedBuilding);
      if (foundBuilding && foundBuilding.floors[this.selectedFloor]) {
        this.dropdownRooms = foundBuilding.floors[this.selectedFloor];
      }
    },
    resetDropdowns() {
      this.dropdownBuildings = [];
      this.dropdownFloors = [];
      this.dropdownRooms = [];
      this.selectedBuilding = '';
      this.selectedFloor = '';
      this.selectedRoom = '';
    },
    goToPage(action) {
  let path = '';
  switch (action) {
    case 'study':
      path = `/study/${this.selectedBuilding}`;
      break;
    case 'toilet':
      path = `/toilet/${this.selectedBuilding}/${this.selectedFloor}`;
      break;
    case 'water':
      path = `/water/${this.selectedBuilding}/${this.selectedFloor}`;
      break;
    case 'conven':
      path = `/conven/${this.selectedBuilding}`;
      break;
    case 'peace':
      path = `/peace/${this.selectedBuilding}`;
      break;
    case 'paper':
      path = `/paper/${this.selectedBuilding}`;
      break;
    // 추가적인 케이스들...
  }
  this.$router.push(path);
}
  }
};
</script>

<style scoped>
.dropdown-container {
  margin: 50px;
  display: flex;  /* 수평 레이아웃 적용 */
  justify-content: space-between; /* 각 항목 사이에 공간 추가 */

}
/* 스타일은 변경 없음 */
.tag-container {
  margin-bottom: 20px;
}

.tag {
  display: inline-block;
  background-color: #eee;
  padding: 5px;
  margin: 5px;
  cursor: pointer;
}

.selected {
  background-color: #007bff;
  color: white;
}
.left {
  overflow: visivle;
}
</style>